@import '../../sass/Constants.scss';

.step {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 220px;

  &:nth-child(3) {
    width: 294px;
  }
}

.step-01 {
  position: absolute;
  top: 67px;
  left: 80%;
  width: 100%;
  height: 12px;
  background-image: url('../../images/icons/arrow-right.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.step-02 {
  position: absolute;
  top: 67px;
  left: 80%;
  width: 100%;
  height: 12px;
  background-image: url('../../images/icons/arrow-right.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.step-03 {
  position: absolute;
  top: 67px;
  left: 80%;
  width: 100%;
  height: 12px;
  background-image: url('../../images/icons/arrow-right.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.step-06 {
  position: absolute;
  top: 40px;
  left: 24%;
  width: 100%;
  height: 12px;
  background-image: url('../../images/icons/arrow-right.svg');
  background-size: contain;
  background-repeat: no-repeat;
  transform: scale(-1);
}

.step-05 {
  position: absolute;
  top: -82px;
  left: 50%;
  width: 100%;
  height: 142px;
  background-image: url('../../images/icons/arrow-down-left.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.step-icon {
  width: 84px;
  height: 99px;
  margin-top: 5px;
  padding-bottom: 15px;
  border-bottom: 2px solid $color-black;
}

.step-number {
  font-size: 17.85px;
  font-weight: $fw-medium;
  line-height: 21.15px;
  color: $color-black;
}

.step-header {
  margin-top: 25px;
  font-size: 30px;
  line-height: 35.55px;
}

.step-description {
  margin-top: 10px;
  font-size: 20px;
  font-weight: $fw-regular;
  line-height: 26px;
}
